import React, {Component} from 'react';
import $ from 'jquery';

class Reflect extends Component {
   constructor() {
      super();
      this.state = {response : false, curSimScore : null};
      this.compareAnswers = this.compareAnswers.bind(this);
      this.clearAnswer = this.clearAnswer.bind(this);
   }
   compareAnswers() {
      let stuAnswer = $('#reflectQuestion').val();
      let sugAnswer = this.props.suganswer;
      let score;
      $('#reflectQuestion').val('');

      const Http = new XMLHttpRequest();
      const url=`https://api.dandelion.eu/datatxt/sim/v1/?text1=${stuAnswer}&text2=${sugAnswer}&token=a4afc7d8c8c74f53820d3691f00c731d`;
      Http.open("GET", url);
      Http.send();

      Http.onreadystatechange = (e) => {
      let start = Http.responseText.indexOf('similarity') + 12;
      let end = Http.responseText.indexOf('"lang":');
      let response = Http.responseText.slice(start, end - 1);
      score = (Number(response) * 100).toFixed(2);

      if (isNaN(score)) score = 0;
      $('#modal-score').text(`Score: ${score} points out of 100.`);
      $('#stu-answer').html(`<strong>Your answer</strong><br /><i>${stuAnswer}</i>`);
      $('#sug-answer').html(`<strong>Suggested answer</strong><br />${sugAnswer}`);
      }
      this.setState({response : true});
   }
   clearAnswer() {
      $('#reflectQuestion').val('');
      this.setState({response : false});
   }
   render() {
      return (
      <div>
      <textarea id='reflectQuestion' placeholder={this.props.reflectQ} className='uk-width-1-1@m' style={{width : '81%', marginBottom : 10, resize : 'none', fontSize : '0.8em', minHeight : '8vh', border : 'none', boxSizing : 'border-box',
      padding : '5px 5px 5px 10px', outline : 'none'}} /><button onClick={this.compareAnswers} className='uk-button uk-button-default uk-button-small' uk-toggle="target: #modal-reflect" style={{minHeight : '8vh', float : 'right', borderTopRightRadius : 5, borderBottomRightRadius : 5, boxShadow : '0 0 3px #aaa',
      border : 'none', width : '15%', display : 'flex', alignItems : 'center', justifyContent : 'center', backgroundColor : 'white', fontSize : '1.2vmax'}}>check</button>

      <div id="modal-reflect" uk-modal='true'>
      <div className="uk-modal-dialog uk-modal-body uk-overflow-auto">
      <button className="uk-modal-close-default" type="button" uk-close='true' onClick={this.clearAnswer}></button>
      <h4 className="uk-modal-title" id='modal-score'> </h4>
      <p className='uk-text-justify' id='stu-answer'></p>
      <p className='uk-text-justify' id='sug-answer'></p>
      </div>
      </div>
      </div>
      );
   }
}

export default Reflect;
