import React, {Component} from 'react';
import * as MyScriptJS from 'myscript'
import 'myscript/dist/myscript.min.css';
import $ from 'jquery';

var socket = null;
var userID = null;
var usernname = null;
var joinInterval;
var banInterval;
var stateInterval;

const editorStyle = {width : '100%', height : '100%', touchAction : 'none'};

const fracStyle = {width : '7.5vh', height : '14vh', fontFamily : 'sans-serif', fontSize : '1.25em', textAlign : 'center', outline : 'none', boxShadow : '0 0 3px #ccc', overflow : 'hidden', display : 'flex', alignItems : 'center', justifyContent : 'center', backgroundColor : '#fafafa', color : 'black'};

const inOutLabelStyleRight = {position : 'absolute', top : 0, right : 0, backgroundColor : 'rgba(21, 160, 133, 0.5)', color : 'white', fontFamily : 'Rubik', fontSize : '0.9em', fontVariant : 'small-caps', borderBottomLeftRadius : '4px', width : '75px', height : '25px', display : 'flex', alignItems : 'center', justifyContent : 'center'};

const clearStyle = {position : 'absolute', bottom : '0px', right : '0px', height : '25px', boxShadow : '0 0 2px #ccc', outline : 'none', fontVariant : 'small-caps', backgroundColor : '#fafafa', color : 'gray', border : 'none', width : '75px', borderTopLeftRadius : '4px', cursor : 'pointer', zIndex : '100'};

const submitStyle = {position : 'absolute', bottom : '35px', right : '0px', height : '25px', boxShadow : '0 0 2px #ccc', outline : 'none', fontVariant : 'small-caps', backgroundColor : '#fafafa', color : 'gray', border : 'none', width : '75px', borderTopLeftRadius : '4px', cursor : 'pointer', zIndex : '100'};

class InteractiveSVG extends Component {
   constructor() {
      super();
      this.state = {storedAnswer : null, SOCKETON : false, numusers : 0, is_teacher : false, users : {}, nnames : {}, curAssigned : false, teacher_exists : false};
      this.handleAnswers = this.handleAnswers.bind(this);
      this.clearAnswer = this.clearAnswer.bind(this);
      this.storeAnswer = this.storeAnswer.bind(this);
      this.startSocket = this.startSocket.bind(this);
      this.receiveUser = this.receiveUser.bind(this);
      this.endUser = this.endUser.bind(this);
      this.sendAssign = this.sendAssign.bind(this);
      this.receiveAssign = this.receiveAssign.bind(this);
   }
  handleMathAnswer(evt) {
     setTimeout(function() {
     var exports = evt.detail.exports || {};
     var answer = exports['application/x-latex'] || '';
     var ansreg = /(\d+)\/(\d+)/;
     var match = ansreg.exec(answer);
     if (match) answer = answer.replace(ansreg, `\\dfrac {${match[1]}}{${match[2]}}`);
     this.storeAnswer(answer);
     if (this.state.SOCKETON) socket.send(JSON.stringify({action : 'math-answer', msg: answer}));
     }.bind(this), 200);
  }
  componentDidMount() {
    this.editor = MyScriptJS.register(this.refs.editor, {
    recognitionParams: {
    type: 'MATH',
    protocol: 'WEBSOCKET',
    apiVersion: 'V4',
    server: {
    scheme: 'https',
    host: 'webdemoapi.myscript.com',
    applicationKey: '1463c06b-251c-47b8-ad0b-ba05b9a3bd01',
    hmacKey: '60ca101a-5e6d-4159-abc5-2efcbecce059',
    },
    },
    });

    window.addEventListener("resize", () => {this.editor.resize()});
    let inp = document.getElementById('input-area');
    inp.addEventListener('exported', this.handleMathAnswer.bind(this));
    window.addEventListener('touchmove', function() {});
    window.addEventListener('beforeunload', function() {
    socket.send(JSON.stringify({action : 'userend', userID : userID}));
    });
  }
  componentWillUnmount() {
     clearInterval(joinInterval);
  }
  storeAnswer(ans) {
     $('#fracInput').html(`\\(\\mathtt{${ans}}\\)`);
     this.setState({storedAnswer : ans});
     window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);
  }
  handleMessage(event) {
     if (!this.state.SOCKETON) return;
     var message = JSON.parse(event.data);
     if (message.action === 'math-answer') this.storeAnswer(message.msg);
     else if (message.action === 'submit') this.handleReceiveAnswers();
     else if (message.action === 'usercheck') this.receiveUser(message.userID, message.nname, message.tExist);
     else if (message.action === 'userend') this.endUser(message.userID);
     else if (message.action === 'assign') this.receiveAssign(message.userID);
     else if (message.action === 'unassign') this.receiveUnassign();
     else if (message.action === 'emit-state') this.props.updatestate(message.tnum, message.cans);
  }
  startSocket(evt) {
     let roomID = $('#socketID').val();
     let teacher_exists = new Date().getTime();
     if (roomID.match(/[A-Za-z0-9_-]+[T$]$/g) !== null) {
        roomID = roomID.slice(0, -2);
        $('#socketID').val(roomID);
        this.setState({is_teacher : true, teacher_exists : teacher_exists});
        setTimeout(function(){
        if (this.state.is_teacher) {
           stateInterval = setInterval(function() {
           socket.send(JSON.stringify({action : 'emit-state', tnum : this.props.testnum, cans : this.props.curans}));
           }.bind(this), 1580);
        }
        else {
           clearInterval(stateInterval);
        }
        }.bind(this), 3000);
     }
     if (roomID === '' || roomID.match(/[^A-Za-z0-9_-]+/g) !== null) return;
     usernname = $('#nickname').val();
     if (usernname === '') usernname = '*';
     socket = new WebSocket("wss://connect.websocket.in/fraclargersmaller?room_id=" + roomID);
     socket.addEventListener('message', this.handleMessage.bind(this));
     this.setState({SOCKETON : true});
     window.UIkit.modal(document.getElementById('modal-example')).hide();
     userID = String(Math.floor(Math.random() * 1000000));
     joinInterval = setInterval(function(){
        socket.send(JSON.stringify({action : 'usercheck', userID : userID, nname : usernname, tExist : teacher_exists}));
     }, 2000);
  }
  receiveUser(msg, name, tExist) {
     for (var prop in this.state.users) if (this.state.users[prop] === msg) return;
     let newUsers = this.state.users;
     newUsers[String(this.state.numusers + 1)] = msg;
     let newNames = this.state.nnames;
     newNames[String(this.state.numusers + 1)] = name;
     this.setState({numusers : this.state.numusers + 1, users : newUsers, nnames : newNames});

     if (tExist < this.state.teacher_exists) {
        this.setState({is_teacher : false, teacher_exists : true});
     }
  }
  endUser(msg) {
    if (this.state.curAssigned === msg) socket.send(JSON.stringify({action : 'unassign'}));
    $('#' + msg).css({'pointer-events' : 'none', 'visibility' : 'hidden'});
    $('#' + msg).prop('disabled', true);
  }
  handleAnswers() {
     if (this.state.storedAnswer === '') return;
     if (this.state.SOCKETON) socket.send(JSON.stringify({action : 'submit'}));
     var numStartInd = this.state.storedAnswer.indexOf('{') + 1, numEndInd = this.state.storedAnswer.indexOf('}');
     var denomStartInd = this.state.storedAnswer.indexOf('{', numStartInd) + 1, denomEndInd = this.state.storedAnswer.indexOf('}', numEndInd + 1);
     var num = Number(this.state.storedAnswer.slice(numStartInd, numEndInd));
     var denom = Number(this.state.storedAnswer.slice(denomStartInd, denomEndInd));

     if (this.props.testnum % 3 === 1) {

        if (this.props.curans === 1) {
        if (denom === 8 && num > 5) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 2) {
        if (denom === 9 && num < 3 && num >= 0) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 3) {
        if (denom === 1 && num < 6 && num >= 0) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 4) {
        if (denom === 1 && num > 1) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 5) {
        if (denom === 9 && num > 8) {
        this.props.correctans(true);
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
     }
     else if (this.props.testnum % 3 === 2) {

        if (this.props.curans === 1) {
        if (num === 1 && denom > 2) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 2) {
        if (num === 5 && denom < 6 && denom > 0) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 3) {
        if (num === 3 && denom < 8 && denom > 0) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 4) {
        if (num === 1 && denom > 2) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 5) {
        if (num === 8 && denom > 9) {
        this.props.correctans(true);
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
     }
     else if (this.props.testnum % 3 === 0) {

        if (this.props.curans === 1) {
        if (num === 4 && denom > 3) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 2) {
        if (denom === 4 && num > 4) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 3) {
        if (num === 2 && denom > 7) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 4) {
        if (denom === 8 && num > 2) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 5) {
        if (num === 8 && denom > 5) {
        this.props.correctans(true);
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
     }
  }
  handleReceiveAnswers() {
     if (this.state.storedAnswer === '') return;
     var numStartInd = this.state.storedAnswer.indexOf('{') + 1, numEndInd = this.state.storedAnswer.indexOf('}');
     var denomStartInd = this.state.storedAnswer.indexOf('{', numStartInd) + 1, denomEndInd = this.state.storedAnswer.indexOf('}', numEndInd + 1);
     var num = Number(this.state.storedAnswer.slice(numStartInd, numEndInd));
     var denom = Number(this.state.storedAnswer.slice(denomStartInd, denomEndInd));

     if (this.props.testnum % 3 === 1) {

        if (this.props.curans === 1) {
        if (denom === 8 && num > 5) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 2) {
        if (denom === 9 && num < 3 && num >= 0) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 3) {
        if (denom === 1 && num < 6 && num >= 0) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 4) {
        if (denom === 1 && num > 1) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 5) {
        if (denom === 9 && num > 8) {
        this.props.correctans(true);
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
     }
     else if (this.props.testnum % 3 === 2) {

        if (this.props.curans === 1) {
        if (num === 1 && denom > 2) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 2) {
        if (num === 5 && denom < 6 && denom > 0) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 3) {
        if (num === 3 && denom < 8 && denom > 0) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 4) {
        if (num === 1 && denom > 2) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 5) {
        if (num === 8 && denom > 9) {
        this.props.correctans(true);
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
     }
     else if (this.props.testnum % 3 === 0) {

        if (this.props.curans === 1) {
        if (num === 4 && denom > 3) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 2) {
        if (denom === 4 && num > 4) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 3) {
        if (num === 2 && denom > 7) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 4) {
        if (denom === 8 && num > 2) {
        this.props.correctans();
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
        else if (this.props.curans === 5) {
        if (num === 8 && denom > 5) {
        this.props.correctans(true);
        this.editor.clear();

        }
        else $('#submitHandwriting').css('background-color', 'red');
        }
     }
  }
  clearAnswer() {
     this.editor.clear();
     $('#fracInput').css({'color' : '#fafafa'});
     setTimeout(function(){
        $('#fracInput').html('');
        $('#fracInput').css({'color' : 'black'});
     }, 1000);
     $('#submitHandwriting').css('background-color', '#fafafa');
  }
  sendAssign(evt) {
     let blockAll = this.state.curAssigned === 'teacher';
     if (evt.currentTarget.id === 'ban-button' && this.state.is_teacher && !blockAll) {
        $('#ban-button').css({'background-color' : 'rgba(255, 0, 0, 0.8)'});
        $('.userbutton').css({'background-color' : 'lightblue'});
        this.setState({curAssigned : 'teacher'});
        banInterval = setInterval(function(){
           socket.send(JSON.stringify({action : 'assign', userID : 'teacher'}));
        }, 2000);
        return;
     }
     else if (evt.currentTarget.id === 'ban-button' && this.state.is_teacher && blockAll) {
        $('#ban-button').css({'background-color' : 'lightblue'});
        socket.send(JSON.stringify({action : 'unassign'}));
        this.setState({curAssigned : false});
        clearInterval(banInterval);
        return;
     }
     else if ($('#' + evt.currentTarget.id).css('background-color') !== 'rgb(144, 238, 144)') {
        clearInterval(banInterval);
        $('#ban-button').css({'background-color' : 'lightblue'});
        $('#' + evt.currentTarget.id).css({'background-color' : 'lightgreen'});
        $('.userbutton').not('#' + evt.currentTarget.id).css({'background-color' : 'lightblue'});
        socket.send(JSON.stringify({action : 'assign', userID : evt.currentTarget.id}));
        this.setState({curAssigned : evt.currentTarget.id});
     }
     else {
        $('#ban-button').css({'background-color' : 'lightblue'});
        $('#' + evt.currentTarget.id).css({'background-color' : 'lightblue'});
        socket.send(JSON.stringify({action : 'unassign'}));
        this.setState({curAssigned : false});
     }
  }
  receiveAssign(id) {
     if (userID === id && this.state.curAssigned === false) {
        $('#coverCanvas').css({'display' : 'none'});
        $('#assignModal').trigger('click');
        this.setState({curAssigned : true});
     }
     else if (userID === id && this.state.curAssigned === true) {
        this.setState({curAssigned : false});
     }
     else if (userID !== id) {
        $('#coverCanvas').css({'display' : 'inline'});
        this.setState({curAssigned : false});
     }
  }
  receiveUnassign() {
     $('#coverCanvas').css({'display' : 'none'});
     this.setState({curAssigned : false});
  }
  render() {

    let socketStyle;
    let userbuttons = [];

    if (this.state.SOCKETON) socketStyle = {backgroundColor : '#00ff00'};
    else socketStyle = {backgroundColor : '#D3D3D3'};
    if (this.state.is_teacher) socketStyle = {backgroundColor : '#ff00ff'};

    let userlist = Object.keys(this.state.users);
    let namelist = Object.keys(this.state.nnames);
    for (var a = 0; a < userlist.length; a++) {
    let leftpos = (a + 1) * 35 + 15;
    userbuttons.push(
      <button className='userbutton uk-animation-slide-top' key={a} id={this.state.users[userlist[a]]} uk-tooltip={`title: ${this.state.nnames[namelist[a]]}`} style={{left : leftpos}} onClick={this.sendAssign}>
      <span uk-icon="happy"></span>
      </button>
    );
    }

    return (
    <div id='interactive' uk-grid='true' className='uk-grid-collapse'>
    <div id='input-output' className='uk-width-1-1' style={{height : '100%', touchAction : 'none'}}>
    <div id='output' style={{borderBottom : '1px solid lightgray', height : '35%', position : 'relative', display : 'flex', alignItems : 'center', justifyContent : 'center', flexDirection : 'column'}}>
    <div style={inOutLabelStyleRight}>fraction</div>
    <div id='fracInput' style={fracStyle}></div>
    </div>
    <div id='input-area' style={{height : '65%', position : 'relative'}}>
    <div style={inOutLabelStyleRight}>write</div>
    <div style={editorStyle} ref="editor" touch-action="none"></div>
    <button id='clearHandwriting' onClick={this.clearAnswer} style={clearStyle}>clear</button>
    <button id='submitHandwriting' onClick={this.handleAnswers} style={submitStyle}>submit</button>
    </div>
    </div>
    <button id='openModal' type="button" uk-toggle="target: #modal-example" style={socketStyle}><span uk-icon="more"></span></button>
    <button id='assignModal' type="button" uk-toggle="target: #modal-assign" style={{display : 'none'}}></button>

    <div id="modal-example" uk-modal='true'>
    <div className="uk-modal-dialog uk-modal-body uk-overflow-auto">
    <button className="uk-modal-close-default" type="button" uk-close='true'></button>
    <h4 className="uk-modal-title">Guided Practice</h4>
    <p className='uk-text-justify'>Enter a Room ID (e.g., mrscala_203). Then click to join an interactive practice session. If you are leading, share the ID for others to join. If you are joining, enter the ID shared with you and enter a name if you want to.</p>
    <p className="uk-text-right"><input id='socketID' placeholder='Room ID'></input></p>
    <p className="uk-text-right"><input id='nickname' placeholder='Name | Nickname' maxLength='10'></input></p>
    <p className='uk-text-justify'>Entering numbers, letters, hyphens, and underscore characters is okay, up to a maximum of 20 characters.</p>
    <p className="uk-text-right">
    <button className="uk-button uk-button-primary" type="button" onClick={this.startSocket}>Join Session</button>
    </p>
    </div>
    </div>

    <div id="modal-assign" uk-modal='true'>
    <div className="uk-modal-dialog uk-modal-body uk-overflow-auto">
    <button className="uk-modal-close-default" type="button" uk-close='true'></button>
    <h4 className="uk-modal-title">Your Turn</h4>
    <p className='uk-text-justify'>You're up! You have been selected to show your work. Go get 'em!</p>
    </div>
    </div>
    {this.state.is_teacher ? userbuttons : null}
    {this.state.is_teacher ? <button className='banbutton' id='ban-button' style={{left : 5}} onClick={this.sendAssign}><span uk-icon="ban"></span></button> : null}
    <div id='coverCanvas'></div>
    </div>
    );
  }
}

export default InteractiveSVG;
